* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: Roboto, sans-serif;
}

.header {
  justify-content: space-between;
  align-items: center;
  width: 100%;
  display: flex;
}

.header__logo {
  width: 40px;
  height: 40px;
  margin-left: 50px;
  position: relative;
}

.header__image {
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
}

.nav {
  margin-right: 50px;
  display: flex;
}

.nav__list {
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
}

.nav__item {
  text-align: center;
  align-items: center;
  height: 60px;
  display: flex;
  position: relative;
}

.nav__item:not(:first-child) {
  margin-left: 10px;
}

.nav__item:not(:last-child) {
  margin-right: 10px;
}

.nav__link {
  color: #060b35;
  text-align: center;
  text-transform: uppercase;
  align-items: center;
  height: 100%;
  font-family: Roboto, sans-serif;
  font-size: 12px;
  font-weight: 500;
  text-decoration: none;
  transition: all .3s;
  display: flex;
}

.nav__link:hover, .is-active {
  color: #00acdc;
}

.is-active:after {
  content: "";
  background-color: #00acdc;
  border-radius: 100px;
  width: 100%;
  height: 4px;
  display: block;
  position: absolute;
  bottom: 0;
}

.main {
  justify-content: center;
  align-items: center;
  padding: 50px 40px;
  display: flex;
}

.main__container {
  grid-template-columns: repeat(1, 200px);
  justify-content: center;
  gap: 46px 48px;
  display: grid;
}

@media (width >= 488px) {
  .main__container {
    grid-template-columns: repeat(2, 200px);
  }
}

@media (width >= 768px) {
  .main__container {
    grid-template-columns: repeat(3, 200px);
  }
}

@media (width >= 1024px) {
  .main__container {
    grid-template-columns: repeat(4, 200px);
  }
}

.stars {
  align-items: center;
  display: flex;
}

.stars__star {
  background-image: url("star.822d561e.svg");
  background-position: center;
  background-repeat: no-repeat;
  width: 16px;
  height: 16px;
  margin-right: 4px;
}

.stars--1 > .stars__star:nth-child(-n+1), .stars--2 > .stars__star:nth-child(-n+2), .stars--3 > .stars__star:nth-child(-n+3), .stars--4 > .stars__star:nth-child(-n+4), .stars--5 > .stars__star:nth-child(-n+5) {
  background-image: url("star-active.edda4c43.svg");
}

